import React from "react"
import Sponsors from "../sponsors/sponsors";
import contentsTableStyle from "./contentstable.module.css"

const ContentsTableItem = ({ contentTableItem, section }) => {

    console.log(section)

    let listItem;

    if (section === contentTableItem.caption) {
        listItem = <li className={ contentsTableStyle.selectedItem }><a href={ contentTableItem.link }>{ contentTableItem.caption }</a></li>
    } else {
        listItem = <li><a href={ contentTableItem.link }>{ contentTableItem.caption }</a></li>
    }

    return (
        <>
            { listItem }
            { contentTableItem.children.length > 0 &&
                <ContentsTableInternal contentsTableData={ contentTableItem.children } section={ section }></ContentsTableInternal>
            }
        </>
    )
}

const ContentsTableInternal = ({ contentsTableData, section }) => {
    let c = 0;

    return (
        <ul>
            { contentsTableData.map(item => <ContentsTableItem contentTableItem={ item } section={ section } key={ c++ }></ContentsTableItem>) }
        </ul>
    )
}

const ContentsTable = ({ contentsTableData, section }) => {
    return (
        <nav className={ contentsTableStyle.docContentsTable }>
            <ContentsTableInternal contentsTableData={ contentsTableData } section={ section }></ContentsTableInternal>
            <Sponsors></Sponsors>
        </nav>
    )
}

export default ContentsTable