import React from "react"
import layoutStyle from "./docslayout.module.css"
import DocLogo from "../doclogo/doclogo"
import { Container } from "react-bootstrap"
import ContentsTable from "../contentstable/contentstable"
import DocsFooter from "../footer/docsfooter"
import { Image } from "react-bootstrap"
import SiteHat from "../../common/sitehat/sitehat"
import LanguageSelector from "../../common/languageselector/languageselector"

//TODO: import from site config
//TODO: add meta info for each section
const contentsTable = [
    {
        caption: "Home",
        link: "/",
        children: []
    },
    {
        caption: "Documentation",
        link: "/docs/",
        children: [
            {
                caption: "Quick Start",
                link: "/docs/quickstart/",
                children: []
            }
        ]
    }
]

class DocLayot extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            isSidebarVisible: false
        }
    }

    toggleSidebarVisibility() {
        this.setState(prevState => ({ isSidebarVisible: !prevState.isSidebarVisible }))
    }
    
    render() {
        const children = this.props.children
        const section = this.props.section

        let { isSidebarVisible } = this.state
        let sidebarStyle = layoutStyle.sidebar

        if (isSidebarVisible) {
            sidebarStyle += " " + layoutStyle.activeSidebar
        }

        return (
            <>
                <SiteHat />
                <div className={ layoutStyle.layout }>
                    <header className={ layoutStyle.docsHeader }>
                        <DocLogo />
                        <LanguageSelector docs={ true }/>
                        <nav className={ layoutStyle.docsMobileHeader }> 
                            <div className={ layoutStyle.sidebarCloseWrap }>
                                <Image src="/images/MenuShow.svg" width="50" height="50" onClick={ () => this.toggleSidebarVisibility() }></Image>
                            </div>
                        </nav>
                    </header>
                    <aside className={ sidebarStyle }>
                        <div className={ layoutStyle.sidebarCloseWrap }>
                            <Image src="/images/MenuClose.svg" width="50" height="50" onClick={ () => this.toggleSidebarVisibility() }></Image>
                        </div>
                        <ContentsTable contentsTableData={ contentsTable } section={ section }></ContentsTable>
                    </aside>
                    <div className={ layoutStyle.layoutWrap }>
                        <div className={ layoutStyle.docsLayout }>
                            <Container>
                                <h1>{ section }</h1>
                                <div className={ layoutStyle.docsDecorTop }></div>
                                <main>
                                    <article>
                                        { children }
                                    </article>
                                </main>
                            </Container>
                            <DocsFooter />
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

export default DocLayot